.card-list {
  width: auto;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 22% 22% 22% 22%;
  grid-gap: 4%;
}
@media screen and (max-width: 768px) {
  .card-list {
    width: auto;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 47% 47%;
    grid-gap: 6%;
  }
  
}
@media screen and (max-width: 480px) {
  .card-list {
    width: 80%;
    margin: auto;
    grid-template-columns: 100%;
    grid-gap: 5%;
  }
  
}
